class NamronRegulatorCard extends HTMLElement {
    // Whenever the state changes, a new `hass` object is set. Use this to
    // update your content.
    set hass(hass) {
        // Initialize the content if it's not there yet.
        const entityId = this.config.entity;
        const state = hass.states[entityId];
        const stateStr = state ? state.state : "unavailable";
        const attributes = state ? state.attributes : null;
        const displayName = attributes ? attributes.friendlyName : entityId;

        if (!this.content) {
            this.innerHTML = `
        <ha-card header="${displayName}">
          <div class="card-content"></div>
        </ha-card>
      `;
            this.content = this.querySelector("div");
        }

        this.content.innerHTML = `
      The state of the regulator is ${stateStr}!
    `;
    }

    // The user supplied configuration. Throw an exception and Home Assistant
    // will render an error card.
    setConfig(config) {
        if (!config.entity) {
            throw new Error("You need to define an entity");
        }
        this.config = config;
    }

    // The height of your card. Home Assistant uses this to automatically
    // distribute all cards over the available columns.
    getCardSize() {
        return 3;
    }
}

customElements.define("namron-regulator-card", NamronRegulatorCard);